<template>
  <div class="container">
    <div class="random-number" :style="{ fontSize: fontSize + 'px', fontFamily: fontFamily }">
      {{ randomNumber }}
    </div>
    <div class="text-representation">
      {{ textRepresentation }}
    </div>
    <div>
      <label class="easy">Easy</label>
      <input class="slider" type="range" min="500" max="5000" v-model="interval" 
@input="adjustInterval()" step="100">
      <label class="hard">Hard</label>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      randomNumber: 0,
      fontSize: 72,
      fontFamily: 'Arial Black',
      textRepresentation: '',
      interval: 3000
    }
  },
  mounted() {
    this.timer = setInterval(() => {
      this.randomNumber = Math.floor(Math.random() * 100)
      this.textRepresentation = this.numberToText(this.randomNumber)
    }, 6000 - this.interval)
  },
  beforeUnmount() {
    clearInterval(this.timer)
  },
  methods: {
    adjustInterval() {
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.randomNumber = Math.floor(Math.random() * 100)
        this.textRepresentation = this.numberToText(this.randomNumber)
      }, 6000 - this.interval)
    },
    numberToText(num) {
      const ones = [
        'zero',
        'un',
        'deux',
        'trois',
        'quatre',
        'cinq',
        'six',
        'sept',
        'huit',
        'neuf',
        'dix',
        'onze',
        'douze',
        'treize',
        'quatorze',
        'quinze',
        'seize',
        'dix-sept',
        'dix-huit',
        'dix-neuf'
      ]
      const tens = [
        '',
        '',
        'vingt',
        'trente',
        'quarante',
        'cinquante',
        'soixante',
        'soixante',
        'quatre-vingt',
        'quatre-vingt'
      ]

      if (num === 0) return 'z√©ro'
      if (num === 100) return 'cent'
      if (num < 20) return ones[num]
      if (num % 10 === 0) return tens[Math.floor(num / 10)]
      if (num > 69 & num < 80) return `soixante-${ones[num % 10 + 10]}`
      if (num > 79) return `quatre-vingt-${ones[num % 10 + 10]}`
      return `${tens[Math.floor(num / 10)]}-${ones[num % 10]}`
    }


  }
}
</script>



