.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.random-number {
  font-size: 72px;
  font-family: 'Arial Black', sans-serif;
  text-align: center;
}

.text-representation {
  font-size: 24px;
  font-family: 'Arial', sans-serif;
  text-align: center;
  margin-top: 20px;
}

.slider {
  width: 300px;
  margin: 20px 0;
}

.easy {
  float: left;
}

.hard {
  float: right;
}
</style>

